let Stats = require('stats.js')
let THREE = require('three')
window.THREE = THREE
require('three/examples/js/loaders/OBJLoader')

let stats = new Stats();
stats.showPanel( 0 ); // 0: fps, 1: ms, 2: mb, 3+: custom
document.body.appendChild( stats.dom );


const BG_COLOR = "#4e4a59"
const CRUST_COLOR = "#f4c082"
const PEPPERONI_COLOR = "#ff5454"
const CHEESE_COLOR = "#ffff7c"

let deg2rad = deg => deg * Math.PI / 180
let rand = (min = 0.0, max = 1.0) => min + Math.random() * ( max - min )

function loadPizzaAsset() {
  return new Promise((resolve, reject) => {
    let loader = new THREE.OBJLoader();

    loader.load( 'pizza.obj', function (pizza) {
      const Material = THREE.MeshBasicMaterial

      let materialsMap = {
        crust: new Material( {  color: CRUST_COLOR } ),
        pepperoni: new Material( {  color: PEPPERONI_COLOR } ),
        cheese: new Material( {  color: CHEESE_COLOR } ),
      }

      let objectsMap = {
        crust: pizza.children.filter(({ name }) => name.match(/crust/i)),
        pepperoni: pizza.children.filter(({ name }) => name.match(/pepperoni/i)),
        cheese: pizza.children.filter(({ name }) => name.match(/cheese/i)),
      }

      Object.keys(objectsMap).forEach(type => {
        let objects = objectsMap[type]
        let material = materialsMap[type]

        objects.forEach(object => {
          object.material = materialsMap[type]
        })
      })

      resolve(pizza);
    }, undefined, function ( error ) {
      reject(error)
    });
  })
}

function initializeScene({element, objects, onRender}) {
  let scene = new THREE.Scene();

  let camera = new THREE.PerspectiveCamera( 75, window.innerWidth/window.innerHeight, 0.1, 1000 );
  camera.position.z = 4;

  //let renderer = new THREE.WebGLRenderer({antialias:true});
  let renderer = new THREE.WebGLRenderer();

  renderer.setClearColor(BG_COLOR);

  renderer.setSize( window.innerWidth, window.innerHeight );

  element.appendChild( renderer.domElement );

  let ambientLight = new THREE.AmbientLight(0x110000);
  scene.add(ambientLight);

  objects.forEach(obj => scene.add(obj));

  // Render Loop
  let render = function () {
    stats.begin();
    onRender()
    renderer.render(scene, camera);
    stats.end();

    requestAnimationFrame( render );
  };

  render()
}

const spawnPizza = (pizza) => {
  pizza.position.x = rand(-5, 5)
  pizza.position.y = 10 + rand(-1, 1)
  pizza.position.z = 0

  pizza.rotation.x = Math.random()
  pizza.rotation.y = Math.random()
  pizza.rotation.z = Math.random()

  return pizza
}

class Pizza {
  constructor({ prefab }) {
    this.object = prefab.clone();
    this.respawn();
  }

  respawn() {
    this.deltaRotationX = rand(0.001, 0.01)
    this.deltaRotationY = rand(0.001, 0.01)
    this.deltaRotationZ = rand(0.001, 0.01)
    this.deltaPositionY = rand(0.001, 0.025)

    this.object.position.x = rand(-5, 5)
    this.object.position.y = 10 + rand(-1, 1)
    this.object.position.z = 0

    this.object.rotation.x = rand()
    this.object.rotation.y = rand()
    this.object.rotation.z = rand()
  }

  update() {
    this.object.rotation.x += this.deltaRotationX;
    this.object.rotation.y += this.deltaRotationY;
    this.object.rotation.z += this.deltaRotationZ;

    this.object.position.y -= this.deltaPositionY;

    if (this.object.position.y <= -10) {
      this.respawn()
    }
  }
}

export async function renderPizzaScene(element) {
  let prefab = await loadPizzaAsset()

  let pizzas = Array.from({ length: 10 }).map(_ => new Pizza({ prefab }))

  function onRender() {
    pizzas.forEach(pizza => pizza.update())
  }

  initializeScene({
    element,
    onRender,
    objects: pizzas.map(({ object }) => object)
  })
}
